export type StyleType = 'BOLD' | 'ITALIC' | 'STRIKE-THROUGH' | 'HEADER-1' | 'HEADER-2';
export type StyleMapType<T = any> = { [S in StyleType]: T };
export type PartialStyleMapType<T = any> = { [S in StyleType]?: T };
export type Features =
  | 'lists'
  | 'headers'
  | 'inline-styling'
  | 'images'
  | 'links'
  | 'tables'
  | 'cut-copy-paste'
  | 'indentation';
